// import LERequest from './LERequest'
import LERequest from './api-invoker'
export default {
    ConsoleON: true,
    request: (host, herder, apiAddress, type) => {
        return new LERequest(host, herder, type).api(apiAddress)
    },
    empty: (o) => {
        if (o === undefined || o === "" || o === null)
            return true;
        return false;
    },
    urlParam: (params) => {
        return Object.keys(params).map((key) => {
            if (this.empty(params[key]))
                return encodeURIComponent(key) + '=';
            if (typeof params[key] === 'object')
                return encodeURIComponent(key) + '=' + encodeURIComponent(JSON.stringify(params[key]));
            return encodeURIComponent(key) + '=' + encodeURIComponent(params[key]);
        }).join('&')
    },
    uuid: (len, radix) => {
        let chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
        let uuid = [], i;
        radix = radix || chars.length;
        if (len) {
            // Compact form
            for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
        } else {
            // rfc4122, version 4 form
            let r;

            // rfc4122 requires these characters
            uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
            uuid[14] = '4';

            // Fill in random data.  At i==19 set the high bits of clock sequence as
            // per rfc4122, sec. 4.1.5
            for (i = 0; i < 36; i++) {
                if (!uuid[i]) {
                    r = 0 | Math.random() * 16;
                    uuid[i] = chars[(i === 19) ? (r & 0x3) | 0x8 : r];
                }
            }
        }
        return uuid.join('');
    },
    assign(p) {
        let c;
        if ((typeof p === 'object' && p !== null)) {
            if ((Array.isArray(p)))
                c = [];
            else
                c = {};
            for (var i in p) {
                if (typeof p[i] === 'object' && !i.indexOf("_") !== 0) {
                    c[i] = this.assign(p[i]);
                } else {
                    c[i] = p[i];
                }
            }
        } else {
            c = p;
        }
        return c;
    },
    compare(a, b) {
        let context = this;
        if (a === b)
            return true;
        if (!(a instanceof Object && b instanceof Object))
            return false;
        if (Object.keys(a).length !== Object.keys(b).length)
            return false;
        let keys = Object.keys(a);
        for (let i = 0; a !== undefined && i < keys.length; i++) {
            if (a[keys[i]] !== b[keys[i]]) {
                if (a[keys[i]] instanceof Object && b[keys[i]] instanceof Object) {
                    if (!context.compare(a[keys[i]], b[keys[i]]))
                        return false;
                } else {
                    return false;
                }
            }
        }
        return true;
    }
}