import LEUtils from "./LEUtils";
import * as pako from "pako";

function getErrorText(error) {
    if (typeof(error) === "string") {
        try {
            let errorMessage = JSON.parse(error);
            return errorMessage.errormsg || error;
        } catch (e) {
            return error;
        }
    } else {
        return JSON.stringify(error);
    }
}

export default class LERequest {
    constructor(apiHost, defaultHeaders, isCompress) {
        this.onSuccess = undefined;
        this.onError = undefined;
        this.apiHost = undefined;
        this.method = undefined;
        this.endPoint = undefined;
        this.data = undefined;
        this.dataType = undefined;
        this.defaultHeaders = undefined;
        this.methodMap = {
            0: "HEAD",
            1: "GET",
            2: "DELETE",
            3: "POST",
            4: "PUT",
            5: "PATCH"
        };
        this.apiHost = apiHost;
        this.defaultHeaders = defaultHeaders || {};
        this.isCompress = isCompress;
    }

    api(endPoint) {
        this.endPoint = endPoint;
        return this;
    };

    success(func) {
        this.onSuccess = func;
        return this;
    };

    error(func) {
        this.onError = func;
        return this;
    };

    head() {
        this.method = 0;
        this.exec();
    };

    get() {
        this.method = 1;
        this.exec();
    };

    delete() {
        this.method = 2;
        this.exec();
    };

    post(data, dataType) {
        this.method = 3;
        this.data = data || {};
        this.dataType = dataType || LERequest.dataTypeMap.urlForm;
        this.exec();
    };

    put(data, dataType) {
        this.method = 4;
        this.data = data || {};
        this.dataType = dataType || LERequest.dataTypeMap.urlForm;
        this.exec();
    };

    patch(data, dataType) {
        this.method = 5;
        this.data = data || {};
        this.dataType = dataType || LERequest.dataTypeMap.urlForm;
        this.exec();
    };


    exec() {
        let context = this;
        this.defaultHeaders['Access-Control-Allow-Origin'] = '*'
        let headers = new Headers(this.defaultHeaders);
        headers.append('Accept', 'application/json');
        headers.append('Access-Control-Allow-Origin','*')
        let request = {
            method: this.methodMap[this.method],
            mode: "cors",
            credentials: "include",
        };
        console.log('headers',request)
        headers.append('Content-Type', LERequest.dataTypeMap.json);
        if (this.method > 2) {
            headers.append("Content-Type", this.dataType);
            if (this.dataType === LERequest.dataTypeMap.urlForm && this.data instanceof Object) {
                this.data = LEUtils.urlParam(this.data);
            }
            if (this.isCompress) {
                request["body"] = pako.gzip(this.data);
                headers.append("Content-Encoding", "gzip");
            } else {
                request["body"] = this.data;
            }

        }
        request["headers"] = headers;
        console.log('>>>', this.apiHost + this.endPoint, request.headers ,new Headers({a:'a'}))
        fetch(this.apiHost + this.endPoint, request).then(function (response) {
            if (response.status >= 400) {
                response.text().then(function (text) {
                    if (context.onError !== undefined)
                        context.onError(response.status, getErrorText(text));
                });
            } else {
                context.onSuccess(response.status, response);
            }
        }).catch(function (error) {
            if (context.onError !== undefined)
                context.onError("-999", getErrorText(error));
            else
                console.log("-999", error)

        });
    };
}

LERequest.dataTypeMap = {
    urlForm: "application/x-www-form-urlencoded",
    multipartForm: "multipart/form-data",
    json: "application/json"
};