import React, {Component} from 'react';
import axios from 'axios'
import LEUtils from './LEUtils'

let http = {
    post: '',
    get: ''
}
http.post = function (api, data) {
    let params = LEUtils.urlParam(data || {})
    return new Promise((resolve, reject) => {
        axios.post(api, params).then((res) => resolve(res)).catch((error) => reject(error))
    })
}
http.get = function (api, data) {
    let params = LEUtils.urlParam(data || {})
    return new Promise((resolve, reject) => {
        axios.get(api, params).then((res) => resolve(res)).catch((error) => reject(error))
    })
}
export default http